  }
  
  .scheduleEvent-section input:focus + .slider {
	box-shadow: 0 0 1px #00bbc4;
  }
  
  .scheduleEvent-section input:checked + .slider:before {
	-webkit-transform: translateX(26px);
	transform: translateX(26px);
  }
  
  .scheduleEvent-section .slider.round {
	border-radius: 34px;
	width: 50px;
  }
  
  .scheduleEvent-section .slider.round:before {
	border-radius: 50%;
  }

